body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[data-reactroot],
#root {
  height: 90% !important;
  height: calc(100% - 65px) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  min-height: 100%;
  position: relative;
}

body,
.main {
  height: 100%;
  background-color: #242426;
  color: whitesmoke;
}

.App-link {
  color: #61dafb;
}

nav.first-layer {
  z-index: 1;
}

@-webkit-keyframes menu_loading {
  0% {
    background-color: rgba(144, 202, 249, 0.1);
    color: #90caf9;
  }

  50% {
    background-color: rgba(144, 202, 249, 0.5);
    color: #e3f2fd;
  }

  100% {
    background-color: rgba(144, 202, 249, 0.1);
    color: #90caf9;
  }
}

@keyframes menu_loading {
  0% {
    background-color: rgba(144, 202, 249, 0.1);
    color: #90caf9;
  }

  50% {
    background-color: rgba(144, 202, 249, 0.5);
    color: #e3f2fd;
  }

  100% {
    background-color: rgba(144, 202, 249, 0.1);
    color: #90caf9;
  }
}

a.navigation_menu.active {
  /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    rgba(30, 87, 153, 0) 0%,
    rgba(144, 202, 249, 0.8) 50%,
    rgba(125, 185, 232, 0) 100%
  );
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    rgba(30, 87, 153, 0) 0%,
    rgba(144, 202, 249, 0.8) 50%,
    rgba(125, 185, 232, 0) 100%
  );
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#001e5799', endColorstr='#007db9e8', GradientType=0);
  /* IE6-9 */
  border-radius: 3px;
}

a.navigation_menu:hover {
  /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    rgba(30, 87, 153, 0) 0%,
    rgba(144, 202, 249, 0.8) 50%,
    rgba(125, 185, 232, 0) 100%
  );
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    rgba(30, 87, 153, 0) 0%,
    rgba(144, 202, 249, 0.8) 50%,
    rgba(125, 185, 232, 0) 100%
  );
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#001e5799', endColorstr='#007db9e8', GradientType=0);
  /* IE6-9 */
  -webkit-animation: menu_loading 3s infinite;
          animation: menu_loading 3s infinite;
  border-radius: 3px;
}

div.navigation_divider {
  border-left: 1px solid #38546d;
  border-right: 1px solid #16222c;
  height: 40px;
}

.work-in-progress {
  vertical-align: middle;
  margin: auto;
}

.work-in-progress div {
  display: block;
  text-align: center;
  margin: auto;
}

.work-in-progress img {
  object-fit: scale-down;
  display: block;
  text-align: center;
  margin: auto;
}

@-webkit-keyframes loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.1;
  }

  100% {
    opacity: 1;
  }
}

@keyframes loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.1;
  }

  100% {
    opacity: 1;
  }
}

div.loader {
  -webkit-animation: loading 3s infinite;
          animation: loading 3s infinite;
}

div.error {
  max-width: 340px;
}

