.moon {
  width: 50px;
  height: 50px;
  position: fixed;
  left: 700px;
  left: var(--left-margin, 700px);
  z-index: -1px;
  z-index: var(--z-index, -1px);
  background-image: url("http://pngimg.com/uploads/moon/moon_PNG20.png");
  background-size: cover;
  -webkit-animation: animMoon 300s linear infinite;
          animation: animMoon 300s linear infinite; }

@-webkit-keyframes animStar {
  from {
    -webkit-transform: translate(0px, -1000px);
            transform: translate(0px, -1000px); }
  to {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px); } }

@keyframes animStar {
  from {
    -webkit-transform: translate(0px, -1000px);
            transform: translate(0px, -1000px); }
  to {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px); } }

@-webkit-keyframes animMoon {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  to {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px); } }

@keyframes animMoon {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  to {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px); } }

canvas {
  position: absolute;
  top: 0;
  left: 0; }

#terCanvas {
  color: #fff;
  z-index: 1; }

@media only screen and (min-width: 600px) {
  .profile-header {
    margin: 0 auto;
    width: 300px;
    position: absolute;
    height: calc(90% + 40px);
  }

  .profile-body {
    width: auto;
    min-height: 100%;
    width: calc(100% - 300px);
    position: relative;
    float: right;
  }
}

@media only screen and (max-width: 600px) {
  .profile-header {
    margin: 0 auto;
  }

  .profile-body {
    position: relative;
  }
}

.profile-header.header {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  min-width: 300px;
  background: #343434;
}

.profile-header > .profile-picture {
  margin: 20px auto 10px;
  display: block;
}

.profile-header > .profile-picture button[type='button'] {
  margin: 0 auto;
  display: block;
}

.profile-header > .profile-picture button[type='button'] img {
  width: 120px !important;
  height: 120px !important;
}

.header h4 {
  width: 300px;
  margin: 5px auto;
  text-align: center;
}

.header p {
  text-align: center;
  font-size: 0.8em;
  margin-top: 10px;
  margin-bottom: 20px;
}

.profile-body.nav-tabs {
  border-bottom: 0;
}

.profile-body.nav .active {
  color: whitesmoke;
  font-weight: bold;
  border-color: #242426;
}

hr.breaker {
  border: 0px;
  height: 1px;
  background: #90caf9;
  background-image: -webkit-linear-gradient(
    left,
    rgba(144, 202, 249, 0.1),
    #2196f3,
    rgba(144, 202, 249, 0.1)
  );
  background-image: -o-linear-gradient(
    left,
    rgba(144, 202, 249, 0.1),
    #2196f3,
    rgba(144, 202, 249, 0.1)
  );
  margin-top: 4px;
  margin-bottom: 4px;
}

.profile-picture {
  padding: 6px;
}

.profile-picture button[type='button'] {
  border: none;
  background: none;
  color: inherit;
  padding: 0;
  cursor: pointer;
}

.profile-picture button[type='button'] img {
  object-fit: cover;
  display: block;
  margin: 0 auto;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 2px solid #909090;
}

.work-container .col div.content {
  padding: 5px;
  margin: 5px;
  font-size: 0.8em;
}

.work-container .col div.content ul {
  margin-bottom: 6px;
}

.work-container .center {
  padding: 5px;
}

#pdf-container {
  height: 100%;
  width: 100%;
  position: fixed;
}

