$colors: #fff, #9db4ff, #a2b9ff, #a7bcff, #aabfff, #afc3ff, #baccff, #c0d1ff, #cad8ff, #e4e8ff, #edeeff, #fbf8ff, #fff9f9, #fff5ec, #fff4e8, #fff1df, #ffebd1, #ffd7ae;

// n is number of stars required
@function multiple-box-shadow ($n) {
  $value: '#{random(2000)}px #{random(2000)}px #{random(1)}px #{nth($colors, random(length($colors)))}';
  @for $i from 2 through $n {
    $value: '#{$value} , #{random(2000)}px #{random(2000)}px #{random(3) - 1}px #{nth($colors, random(length($colors)))}'; }

  @return unquote($value); }

$shadows-small:  multiple-box-shadow(2000);
$shadows-medium: multiple-box-shadow(300);
$shadows-big:    multiple-box-shadow(100);

.stars {
  width: 1px;
  height: 1px;
  position: fixed;
  left: var(--left-margin, 0);
  z-index: var(--z-index, -1px);
  background: transparent;
  box-shadow: $shadows-small;
  animation			: animStar 150s linear infinite;

  &:after {
    content: " ";
    position: fixed;
    top: 2000px;
    width: 1px;
    height: 1px;
    z-index: var(--z-index, -1px);
    background: transparent;
    box-shadow: $shadows-medium; } }

.stars2 {
  width: 2px;
  height: 2px;
  position: fixed;
  left: var(--left-margin, 0);
  z-index: var(--z-index, -1px);
  background: transparent;
  box-shadow: $shadows-medium;
  animation			: animStar 200s linear infinite;

  &:after {
    content: " ";
    position: fixed;
    top: 2000px;
    width: 2px;
    height: 2px;
    background: transparent;
    box-shadow: $shadows-medium; } }

.stars3 {
  width: 3px;
  height: 3px;
  border-radius: 1px;
  position: fixed;
  left: var(--left-margin, 0);
  z-index: var(--z-index, -1px);
  background: transparent;
  box-shadow: $shadows-big;
  animation			: animStar 250s linear infinite;

  &:after {
    content: " ";
    position: fixed;
    position: fixed;
    left: var(--left-margin, 0);
    z-index: var(--z-index, -1px);
    top: 2000px;
    width: 3px;
    height: 3px;
    border-radius: 1px;
    background: transparent;
    box-shadow: $shadows-big; } }

.moon {
  width: 50px;
  height: 50px;
  position: fixed;
  left: var(--left-margin, 700px);
  z-index: var(--z-index, -1px);
  background-image: url('http://pngimg.com/uploads/moon/moon_PNG20.png');
  background-size: cover;
  animation			: animMoon 300s linear infinite; }


@keyframes animStar {
  from {
    transform: translate(0px, -1000px); }
  to {
    transform: translate(0px, 0px); } }

@keyframes animMoon {
  from {
    transform: translateY(0px); }
  to {
    transform: translateY(1000px); } }


canvas {
  position: absolute;
  top: 0;
  left: 0; }

#terCanvas {
  color: #fff;
  z-index: 1; }
