.work-in-progress {
  vertical-align: middle;
  margin: auto;
}

.work-in-progress div {
  display: block;
  text-align: center;
  margin: auto;
}

.work-in-progress img {
  object-fit: scale-down;
  display: block;
  text-align: center;
  margin: auto;
}

@keyframes loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.1;
  }

  100% {
    opacity: 1;
  }
}

div.loader {
  animation: loading 3s infinite;
}

div.error {
  max-width: 340px;
}
