@media only screen and (min-width: 600px) {
  .profile-header {
    margin: 0 auto;
    width: 300px;
    position: absolute;
    height: calc(90% + 40px);
  }

  .profile-body {
    width: auto;
    min-height: 100%;
    width: calc(100% - 300px);
    position: relative;
    float: right;
  }
}

@media only screen and (max-width: 600px) {
  .profile-header {
    margin: 0 auto;
  }

  .profile-body {
    position: relative;
  }
}

.profile-header.header {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  min-width: 300px;
  background: #343434;
}

.profile-header > .profile-picture {
  margin: 20px auto 10px;
  display: block;
}

.profile-header > .profile-picture button[type='button'] {
  margin: 0 auto;
  display: block;
}

.profile-header > .profile-picture button[type='button'] img {
  width: 120px !important;
  height: 120px !important;
}

.header h4 {
  width: 300px;
  margin: 5px auto;
  text-align: center;
}

.header p {
  text-align: center;
  font-size: 0.8em;
  margin-top: 10px;
  margin-bottom: 20px;
}

.profile-body.nav-tabs {
  border-bottom: 0;
}

.profile-body.nav .active {
  color: whitesmoke;
  font-weight: bold;
  border-color: #242426;
}

hr.breaker {
  border: 0px;
  height: 1px;
  background: #90caf9;
  background-image: -webkit-linear-gradient(
    left,
    rgba(144, 202, 249, 0.1),
    #2196f3,
    rgba(144, 202, 249, 0.1)
  );
  background-image: -moz-linear-gradient(
    left,
    rgba(144, 202, 249, 0.1),
    #2196f3,
    rgba(144, 202, 249, 0.1)
  );
  background-image: -ms-linear-gradient(
    left,
    rgba(144, 202, 249, 0.1),
    #2196f3,
    rgba(144, 202, 249, 0.1)
  );
  background-image: -o-linear-gradient(
    left,
    rgba(144, 202, 249, 0.1),
    #2196f3,
    rgba(144, 202, 249, 0.1)
  );
  margin-top: 4px;
  margin-bottom: 4px;
}
